/**
 * System Integration Tests for Habit Tracking & Streaks System
 *
 * Tests core system integration without UI dependencies
 */

import { Timestamp } from "firebase/firestore";
import { AnalyticsService } from "../services/firebase/analyticsService";
import { CompletionService } from "../services/firebase/completionService";
import { StreakService } from "../services/firebase/streakService";
import { HabitCompletion } from "../types/habit-streaks";
import { getUserTimezone } from "../utils/dateUtils";

// Mock Firebase
jest.mock("../firebaseConfig", () => ({
    db: {},
    APP_ID: "test-app",
}));

jest.mock("firebase/firestore", () => ({
    collection: jest.fn(),
    doc: jest.fn(),
    getDoc: jest.fn(),
    getDocs: jest.fn(),
    query: jest.fn(),
    where: jest.fn(),
    orderBy: jest.fn(),
    runTransaction: jest.fn(),
    serverTimestamp: jest.fn(() => ({ seconds: Date.now() / 1000 })),
    Timestamp: {
        now: jest.fn(() => ({
            seconds: Date.now() / 1000,
            toMillis: () => Date.now(),
            toDate: () => new Date(),
        })),
        fromDate: jest.fn((date: Date) => ({
            seconds: date.getTime() / 1000,
            toMillis: () => date.getTime(),
            toDate: () => date,
        })),
    },
}));

jest.mock("../services/firebase/collections", () => ({
    getHabitCompletionsCollection: jest.fn(() => ({ path: "completions" })),
    getHabitStreaksCollection: jest.fn(() => ({ path: "streaks" })),
    getHabitAnalyticsCollection: jest.fn(() => ({ path: "analytics" })),
}));

jest.mock("../utils/errorHandling", () => ({
    getUserFriendlyErrorMessage: jest.fn(
        (error) => error.message || "Unknown error",
    ),
    retryWithBackoff: jest.fn((fn) => fn()),
    isOnline: jest.fn(() => true),
}));

describe("Habit Tracking System Integration Tests", () => {
    let streakService: StreakService;
    let completionService: CompletionService;
    let analyticsService: AnalyticsService;

    const mockUserId = "integration-user-123";
    const mockHabitId = "integration-habit-456";
    const mockTimezone = getUserTimezone();

    beforeEach(() => {
        streakService = new StreakService();
        completionService = new CompletionService();
        analyticsService = new AnalyticsService();

        jest.clearAllMocks();

        // Mock successful Firebase operations
        const { runTransaction, getDocs, getDoc } = require("firebase/firestore");
        runTransaction.mockImplementation(async (db, callback) => {
            const mockTransaction = {
                get: jest.fn().mockResolvedValue({
                    exists: () => true,
                    data: () => ({
                        currentStreak: 5,
                        bestStreak: 10,
                        freezesAvailable: 1,
                        freezesUsed: 0,
                        milestones: [],
                    }),
                }),
                set: jest.fn(),
                update: jest.fn(),
            };
            return callback(mockTransaction);
        });

        getDocs.mockResolvedValue({
            docs: [
                {
                    id: "completion1",
                    data: () => ({
                        habitId: mockHabitId,
                        userId: mockUserId,
                        completedAt: Timestamp.now(),
                        timezone: mockTimezone,
                        difficulty: "medium",
                    }),
                },
            ],
        });

        getDoc.mockResolvedValue({
            exists: () => true,
            data: () => ({
                currentStreak: 5,
                bestStreak: 10,
                freezesAvailable: 1,
            }),
        });
    });

    describe("Complete User Flow Integration", () => {
        it("should handle habit creation to first milestone workflow", async () => {
            // Step 1: Record first completion
            const firstCompletion: Omit<HabitCompletion, "id"> = {
                habitId: mockHabitId,
                userId: mockUserId,
                completedAt: Timestamp.now(),
                timezone: mockTimezone,
                difficulty: "medium",
            };

            await expect(
                completionService.recordCompletion(firstCompletion),
            ).resolves.not.toThrow();

            // Step 2: Calculate initial streak
            const initialStreak = await streakService.calculateStreak(
                mockHabitId,
                mockUserId,
            );
            expect(initialStreak).toBeDefined();
            expect(initialStreak.habitId).toBe(mockHabitId);
            expect(initialStreak.userId).toBe(mockUserId);

            // Step 3: Verify analytics calculation
            const analytics = await analyticsService.calculateHabitAnalytics(
                mockHabitId,
                mockUserId,
            );
            expect(analytics).toBeDefined();
            expect(analytics.habitId).toBe(mockHabitId);
            expect(analytics.userId).toBe(mockUserId);
        });

        it("should handle consecutive completions and streak building", async () => {
            // Simulate 7 consecutive days
            const completions: Omit<HabitCompletion, "id">[] = [];
            for (let i = 0; i < 7; i++) {
                const date = new Date();
                date.setDate(date.getDate() - (6 - i));

                completions.push({
                    habitId: mockHabitId,
                    userId: mockUserId,
                    completedAt: Timestamp.fromDate(date),
                    timezone: mockTimezone,
                    difficulty: "medium",
                });
            }

            // Record all completions
            for (const completion of completions) {
                await expect(
                    completionService.recordCompletion(completion),
                ).resolves.not.toThrow();
            }

            // Verify streak calculation
            const streak = await streakService.calculateStreak(
                mockHabitId,
                mockUserId,
            );
            expect(streak.currentStreak).toBeGreaterThanOrEqual(0);
            expect(streak.bestStreak).toBeGreaterThanOrEqual(streak.currentStreak);
        });

        it("should handle streak breaks and recovery", async () => {
            // Create a streak, break it, then restart
            const completions: Omit<HabitCompletion, "id">[] = [];

            // 3 consecutive days
            for (let i = 0; i < 3; i++) {
                const date = new Date();
                date.setDate(date.getDate() - (5 - i));

                completions.push({
                    habitId: mockHabitId,
                    userId: mockUserId,
                    completedAt: Timestamp.fromDate(date),
                    timezone: mockTimezone,
                    difficulty: "easy",
                });
            }

            // Record completions
            for (const completion of completions) {
                await expect(
                    completionService.recordCompletion(completion),
                ).resolves.not.toThrow();
            }

            // Skip a day, then complete today
            const todayCompletion: Omit<HabitCompletion, "id"> = {
                habitId: mockHabitId,
                userId: mockUserId,
                completedAt: Timestamp.now(),
                timezone: mockTimezone,
                difficulty: "easy",
            };

            await expect(
                completionService.recordCompletion(todayCompletion),
            ).resolves.not.toThrow();

            // Verify streak handling
            const streak = await streakService.calculateStreak(
                mockHabitId,
                mockUserId,
            );
            expect(streak).toBeDefined();
        });
    });

    describe("Data Consistency and Validation", () => {
        it("should maintain data consistency across services", async () => {
            const completion: Omit<HabitCompletion, "id"> = {
                habitId: mockHabitId,
                userId: mockUserId,
                completedAt: Timestamp.now(),
                timezone: mockTimezone,
                difficulty: "hard",
            };

            // Record completion
            await completionService.recordCompletion(completion);

            // Get streak data
            const streak = await streakService.getHabitStreak(
                mockHabitId,
                mockUserId,
            );

            // Get analytics
            const analytics = await analyticsService.calculateHabitAnalytics(
                mockHabitId,
                mockUserId,
            );

            // All should reference the same habit and user
            expect(streak.habitId).toBe(mockHabitId);
            expect(streak.userId).toBe(mockUserId);
            expect(analytics.habitId).toBe(mockHabitId);
            expect(analytics.userId).toBe(mockUserId);
        });

        it("should validate input parameters across services", async () => {
            // Test empty habitId
            await expect(
                streakService.calculateStreak("", mockUserId),
            ).rejects.toThrow("Habit ID and User ID are required");

            await expect(
                completionService.getCompletionHistory("", mockUserId),
            ).rejects.toThrow("Habit ID and User ID are required");

            await expect(
                analyticsService.calculateHabitAnalytics("", mockUserId),
            ).rejects.toThrow("Habit ID and User ID are required");

            // Test empty userId
            await expect(
                streakService.calculateStreak(mockHabitId, ""),
            ).rejects.toThrow("Habit ID and User ID are required");

            await expect(
                completionService.getCompletionHistory(mockHabitId, ""),
            ).rejects.toThrow("Habit ID and User ID are required");

            await expect(
                analyticsService.calculateHabitAnalytics(mockHabitId, ""),
            ).rejects.toThrow("Habit ID and User ID are required");
        });

        it("should handle invalid completion timestamps", async () => {
            const futureCompletion: Omit<HabitCompletion, "id"> = {
                habitId: mockHabitId,
                userId: mockUserId,
                completedAt: Timestamp.fromDate(new Date(Date.now() + 86400000)), // Tomorrow
                timezone: mockTimezone,
                difficulty: "easy",
            };

            await expect(
                completionService.recordCompletion(futureCompletion),
            ).rejects.toThrow("Cannot record completions for future dates");
        });
    });

    describe("Performance and Concurrency", () => {
        it("should handle concurrent operations on same habit", async () => {
            const promises: Promise<any>[] = [];

            // Simulate concurrent operations
            promises.push(streakService.calculateStreak(mockHabitId, mockUserId));
            promises.push(
                analyticsService.calculateHabitAnalytics(mockHabitId, mockUserId),
            );
            promises.push(
                completionService.getCompletionHistory(mockHabitId, mockUserId),
            );

            const results = await Promise.allSettled(promises);

            // All operations should complete
            expect(results.length).toBe(3);

            // Most should succeed (allowing for some test variance)
            const successful = results.filter((r) => r.status === "fulfilled");
            expect(successful.length).toBeGreaterThan(0);
        });

        it("should handle multiple users concurrently", async () => {
            const userIds = Array.from({ length: 10 }, (_, i) => `user-${i}`);
            const promises: Promise<any>[] = [];

            for (const userId of userIds) {
                promises.push(streakService.calculateStreak(mockHabitId, userId));
            }

            const results = await Promise.allSettled(promises);
            const successful = results.filter((r) => r.status === "fulfilled");

            // Most operations should succeed
            expect(successful.length).toBeGreaterThan(userIds.length * 0.5);
        });

        it("should complete operations within reasonable time", async () => {
            const startTime = Date.now();

            await Promise.all([
                streakService.calculateStreak(mockHabitId, mockUserId),
                analyticsService.calculateHabitAnalytics(mockHabitId, mockUserId),
                completionService.getCompletionHistory(mockHabitId, mockUserId),
            ]);

            const duration = Date.now() - startTime;

            // Should complete within 5 seconds (generous for test environment)
            expect(duration).toBeLessThan(5000);
        });
    });

    describe("Error Handling and Recovery", () => {
        it("should handle Firebase transaction failures gracefully", async () => {
            // Mock transaction failure
            const { runTransaction } = require("firebase/firestore");
            runTransaction.mockRejectedValueOnce(new Error("Transaction failed"));

            const completion: Omit<HabitCompletion, "id"> = {
                habitId: mockHabitId,
                userId: mockUserId,
                completedAt: Timestamp.now(),
                timezone: mockTimezone,
                difficulty: "medium",
            };

            // Should propagate error appropriately
            await expect(
                completionService.recordCompletion(completion),
            ).rejects.toThrow("Transaction failed");
        });

        it("should handle missing data gracefully", async () => {
            // Mock empty data
            const { getDoc, getDocs } = require("firebase/firestore");
            getDoc.mockResolvedValueOnce({ exists: () => false, data: () => null });
            getDocs.mockResolvedValueOnce({ docs: [] });

            // Should handle missing streak data
            const streak = await streakService.getHabitStreak(
                "nonexistent-habit",
                mockUserId,
            );
            expect(streak).toBeDefined();

            // Should handle empty completion history
            const history = await completionService.getCompletionHistory(
                "nonexistent-habit",
                mockUserId,
            );
            expect(history).toBeDefined();
            expect(history.completions).toEqual([]);
        });
    });

    describe("Multi-Habit Analytics", () => {
        it("should calculate cross-habit consistency scores", async () => {
            const habitIds = ["habit-1", "habit-2", "habit-3"];

            // Create completions for multiple habits
            for (const habitId of habitIds) {
                const completion: Omit<HabitCompletion, "id"> = {
                    habitId,
                    userId: mockUserId,
                    completedAt: Timestamp.now(),
                    timezone: mockTimezone,
                    difficulty: "medium",
                };

                await expect(
                    completionService.recordCompletion(completion),
                ).resolves.not.toThrow();
            }

            // Calculate overall consistency
            const consistencyScore =
                await analyticsService.getOverallConsistencyScore(mockUserId);
            expect(typeof consistencyScore).toBe("number");
            expect(consistencyScore).toBeGreaterThanOrEqual(0);
        });

        it("should handle different difficulty levels correctly", async () => {
            const difficulties: Array<"easy" | "medium" | "hard"> = [
                "easy",
                "medium",
                "hard",
            ];

            for (let i = 0; i < difficulties.length; i++) {
                const completion: Omit<HabitCompletion, "id"> = {
                    habitId: `${mockHabitId}-${difficulties[i]}`,
                    userId: mockUserId,
                    completedAt: Timestamp.now(),
                    timezone: mockTimezone,
                    difficulty: difficulties[i],
                };

                await expect(
                    completionService.recordCompletion(completion),
                ).resolves.not.toThrow();

                const analytics = await analyticsService.calculateHabitAnalytics(
                    `${mockHabitId}-${difficulties[i]}`,
                    mockUserId,
                );
                expect(analytics).toBeDefined();
            }
        });
    });

    describe("Timezone Handling", () => {
        it("should handle different timezones correctly", async () => {
            const timezones = ["America/New_York", "Europe/London", "Asia/Tokyo"];

            for (const timezone of timezones) {
                const completion: Omit<HabitCompletion, "id"> = {
                    habitId: mockHabitId,
                    userId: mockUserId,
                    completedAt: Timestamp.now(),
                    timezone,
                    difficulty: "medium",
                };

                await expect(
                    completionService.recordCompletion(completion),
                ).resolves.not.toThrow();
            }

            // Verify streak calculation handles timezone differences
            const streak = await streakService.calculateStreak(
                mockHabitId,
                mockUserId,
            );
            expect(streak).toBeDefined();
        });
    });
});
